<template>
	<div class="C_wrap">
		<header class="login">
			<div class="h_title"></div>
		</header>
		<div class="main">
			<div class="form_in">
				<form>
					<div class="input_row">
						<input type="text" placeholder="" v-model="User.name" />
					</div>
					<div class="input_row">
						<input type="text" placeholder="" v-model="User.psd" />
					</div>
					<p><a v-link="{path:'/register'}"></a></p>
					<button @click="login"></button>
				</form>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	module.exports = {
		data:function(){
			return {
				User:{
					name:'',
					psd:''
				}
			}
		},
		methods : {
			login : function(){
				this.$http.post('/api/login',{"user":this.User}).then(function(res){
					if(this.User.name == res.data.name && this.User.psd == res.data.psd){
						this.$route.router.go('/index');
					}else{
						alert("!");
					}
				},function(res){
					console.log("!");
				})
			}
		}
	}
</script>
<style scoped>
header.login{
	overflow: hidden;
    display: block;
    z-index: 9999;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 2.8125rem!important;
}
</style>


